import { Component, OnInit } from "@angular/core";

@Component({
    selector: 'app-header',
    templateUrl: './dashboard-header.component.html',
    styleUrls: ['./dashboard-header.component.scss']
})
export class AppHeaderComponent implements OnInit {


    drawnQuote: string;
    quoteVisibility = true;

    quotes: string[] =
        ['"Poraka jest po prostu szans, eby sprbowa jeszcze raz, tym razem bardziej inteligentnie" - Henry Ford',
            '"Zamartwiajc si tym, jak wypadniesz, ju na starcie przegrywasz. Po prostu solidnie trenuj i daj z siebie wszystko" - Usain Bolt',
            '"Motywacja jest tym, co pozwala ci zacz. Nawyk jest tym, co pozwala ci wytrwa" - Jim Ryun',
            '"Charakter to umiejtno realizowania postanowie dugo po tym, jak opad ju entuzjazm, z jakim je powzie" - Robert Cavett',
            '"Twoje szanse na sukces s wprost proporcjonalne do stopnia przyjemnoci, jak czerpiesz z tego, co robisz" - Michael Korda',
            '"Dla mnie, podobnie jak dla wielu innych biegaczy, nie ma linii mety. Biegi si kocz, bieganie - nigdy" - Dean Karnazes',
            '"Dobre rzeczy przychodz z czasem, szczeglnie w bieganiu dugodystansowym" - Bill Dellinger'];


    drawtheQuote = (): void => {
        const quotesCount: number = this.quotes.length;
        const drawnIndex = Math.floor(Math.random() * quotesCount);
        this.drawnQuote = this.quotes[drawnIndex];
        console.log(this.drawnQuote);
    }


    ngOnInit(): void {
        if (window.screen.width < 1341) {
            this.quoteVisibility = false;
        }
        this.drawtheQuote();
    }




}
